import React from "react";
import { ThemeProvider } from "styled-components";
import { GlobalStyle } from "@rk/design-system";
import theme from "./../../config/theme";

const Root = ({ children }) => {
  const globaltheme = {
    ...theme,
    colors: {
      ...theme["light"].colors
    }
  };
  return (
    <ThemeProvider theme={globaltheme}>
      <>
        <GlobalStyle />
        {children}
      </>
    </ThemeProvider>
  );
};

export default Root;
